@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  /* -webkit-font-smoothing: antialiased; */
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  text-decoration: none;
  font-style: normal;
  white-space: normal;
  word-break: break-all;
  vertical-align: top;
}

button {
  outline: none;
  cursor: pointer;
  border: none;
  background: none;
  font-family: "Montserrat", sans-serif;
  vertical-align: top;
  display: inline-block;
}

input {
}

input:focus {
  ouline: none;
  border: none;
}

input::-webkit-input-placeholder {
  color: #aeaeae;
}

input:-ms-input-placeholder {
  color: #aeaeae;
}

textarea::-webkit-input-placeholder {
  color: #aeaeae;
}

textarea:-ms-input-placeholder {
  color: #aeaeae;
}

select {
  background-size: 12px;
  background-position-x: 95%;
  border-radius: 0;
  border: solid 1px #f2f3f5;
  outline: none;
  transition: all 0.2s ease-in-out;
  padding: 0 10px;
  font-family: "Montserrat", sans-serif;
  color: #111;
  font-size: 15px;
  -webkit-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  vertical-align: top;
}

select::-ms-expand {
  display: none;
}

select:focus {
  border: solid 1px #ff6001;
  background-size: 12px;
  background-position-x: 95%;
}

textarea {
  width: 100%;
  height: 80px;
  background: #f2f3f5;
  border-radius: 0;
  border: solid 1px #f2f3f5;
  outline: none;
  transition: all 0.2s ease-in-out;
  font-family: "Montserrat", sans-serif;
  color: #111;
  font-size: 15px;
  padding: 8px 10px;
  line-height: 24px;
}

textarea:focus {
  border: solid 1px #ff6001;
  background: #fff;
}

/*header style*/
.headerWrap {
  width: 100%;
  background: rgba(255, 255, 255, 0);
  position: absolute;
  top: -1px;
  left: 0;
  transition: all 0.2s ease-in-out;
  z-index: 100;
  border-bottom: solid 1px rgba(255, 255, 255, 0.5);
}
.headerInner {
  max-width: 1580px;
  min-width: 280px;
  height: 70px;
  margin: 0 auto;
  padding: 12px 20px;
  position: relative;
}
.headerInner .logo {
  position: absolute;
  left: 20px;
  top: 12px;
}
.headerInner .logo a {
  display: block;
  width: 100%;
  height: 100%;
}

.logoImg {
  display: inline-block;
  width: 45px;
  height: 45px;
  background: url(/static/media/coin.c96be1f1.svg) center no-repeat;
  background-size: contain;
  margin-right: 14px;
}
.logoText {
  display: inline-block;
  font-size: 30px;
  letter-spacing: -0.025em;
  line-height: 45px;
  font-family: "Montserrat", sans-serif;
  color: #fff;
  font-weight: 400;
}
.logoText span {
  color: #ffcd00;
  font-weight: 700;
}
.lnb {
  display: inline-block;
}
.headerRight {
  position: absolute;
  right: 20px;
  top: 12px;
}
.sns {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-right: 10px;
  display: inline-block;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
}
.sns.twitter {
  background: url(/static/media/twitter.f88d04f5.svg) #fff center no-repeat;
  background-size: 25px;
}
.sns.gmail {
  background: url(/static/media/google.0de8c67d.svg) #fff center no-repeat;
  background-size: 25px;
}
.sns > a {
  display: block;
  width: 100%;
  height: 100%;
}
.guide {
  height: 45px;
  border-radius: 22.5px;
  background: #fff;
  display: inline-block;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
}
.guide .iconGuide {
  width: 25px;
  height: 25px;
  vertical-align: top;
  display: inline-block;
  background: url(/static/media/guide.b499079b.svg) #fff left no-repeat;
  background-size: contain;
  margin-right: 10px;
}
.guide a {
  display: inline-block;
  vertical-align: top;
  background-size: 25px;
  line-height: 25px;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  color: #222;
  font-weight: 600;
  width: 100%;
  height: 100%;
  padding: 10px 20px;
}
.moNav {
  display: none;
  text-align: right;
  position: relative;
}
.moNav .lnb {
  position: absolute;
  top: 70px;
  right: 0;
  width: 300px;
  text-align: right;
}
.iconMenu {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  display: inline-block;
  background: url(/static/media/iconHamburger.04e72609.svg) #fff center no-repeat;
  background-size: 25px;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
}
.iconClose {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  display: inline-block;
  background: url(/static/media/iconClose.51c73e22.svg) #fff center no-repeat;
  background-size: 20px;
  position: absolute;
  top: 0;
  right: 0;
}
.moMenu {
  display: none;
}
/*footer style*/
.footerWrap {
  width: 100%;
  background: #222222;
}
.footerArea {
  max-width: 1580px;
  min-width: 280px;
  margin: 0 auto;
  padding: 70px 20px;
}
.footerArea address {
  line-height: 24px;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  color: #fff;
  font-weight: 400;
}

::selection {
  background: #ffcd00;
  color: #fff;
}
@media all and (max-width: 767px) {
  .logoText {
    font-size: 26px;
  }
  .moNav {
    display: inline-block;
  }
  .pcNav {
    display: none;
  }
  .logoImg {
    margin-right: 10px;
  }
}
@media all and (max-width: 470px) {
  .logoText {
    font-size: 18px;
  }
}
@media all and (max-width: 374px) {
  .logoText {
    font-size: 14px;
    line-height: 30px;
  }
  .logoImg {
    width: 30px;
    height: 30px;
  }
  .iconMenu {
    width: 30px;
    height: 30px;
    background-size: 20px;
  }
  .headerInner {
    height: 54px;
  }
  .iconClose {
    width: 30px;
    height: 30px;
    background-size: 14px;
  }
  .moNav .lnb {
    top: 60px;
  }
}

